<template>
    <div id="MusicPlayer" >

        <!--  -->

        <!-- controls -->
        <audio
            :src="'https://music.163.com/song/media/outer/url?id='+playMusic.id+'.mp3'"
            autoplay
            ref="audio"
        >
        </audio>
        <div class="MusicPlayer">
            <div class="player">
                <div class="img">
                    <img :src="playMusic.picUrl">
                </div>
                <div class="text" @click="clickMusicPlayer" >
                    <h3>{{playMusic.name}}</h3>
                    <p>
                        <span
                            class="singer"
                            v-for="(artist, index) in playMusic.artists"
                            :key="index"
                        >
                            <template v-if="index" >
                                /
                            </template>{{ artist.name }}
                        </span>
                    </p>
                </div>
                <div class="suspend">
                    <span class="begin" @click.stop="ClickPaused" >
                        <svg v-if="paused" t="1608273312903" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1841" width="24" height="24"><path d="M871.173953 407.921116L265.394605 22.432744C239.616 7.549023 213.944558 0 189.606698 0c-63.666605 0-106.924651 51.128558-106.924651 127.202233v769.583627c0 76.085581 43.258047 127.21414 106.88893 127.21414 24.33786 0 49.985488-7.537116 76.91907-23.111442l603.743255-384.095256c45.853767-26.481116 71.084651-63.666605 71.084651-104.709953 0-41.102884-25.373767-78.336-70.144-104.162233z m-52.61693 152.587907L250.701395 921.83814c-15.026605 8.680186-29.719814 13.252465-42.019721 13.252465-37.483163 0-41.984-42.73414-41.984-61.106605V150.016c0-18.372465 4.500837-61.106605 42.019721-61.106605 12.311814 0 26.969302 4.584186 40.912372 12.609489l569.97507 362.638883C843.573581 477.993674 857.302326 495.461209 857.302326 512.071442c0 16.574512-13.764465 33.982512-38.745303 48.437581z" p-id="1842" fill="#363636"></path></svg>
                        <svg v-else t="1608273601872" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2268" width="20" height="23"><path d="M259.774512 0h-63.761861c-21.849302 0-39.566884 26.457302-39.566884 59.082419v905.823255c0 32.625116 17.717581 59.082419 39.566884 59.082419h63.761861c21.849302 0 39.566884-26.457302 39.566883-59.082419V59.082419C299.341395 26.457302 281.623814 0 259.774512 0z m568.20093 0h-63.761861c-21.849302 0-39.566884 26.457302-39.566883 59.082419v905.823255c0 32.625116 17.717581 59.082419 39.566883 59.082419h63.761861c21.849302 0 39.566884-26.457302 39.566884-59.082419V59.082419C867.542326 26.457302 849.824744 0 827.975442 0z m15.741023 0" p-id="2269" fill="#363636"></path></svg>
                    </span>
                    <canvas ref="circle" width="50" height="50" ></canvas>
                </div>
            </div>
        </div>


        <PlayPage
            v-if="closeMusic"
            :paused="paused"
            :playMusic="playMusic"
            :currentTime="currentTime"
            :duration="duration"
            :lyric="lyric"
            @music-currentTime="$refs.audio.currentTime = $event"
            @music-suspend="ClickSuspend"
            @close-music='closeMusicJudge'
            @music:left="ClickLeft"
            @music:right="ClickRight"
        ></PlayPage>

    </div>
</template>

<script>
import PlayPage from '@/components/PlayPage.vue'

export default {
    components: {
        PlayPage,
    },
    props: {
        currentMusic:{
            type: Object
        },
        currentHotMusic:{
            type: Object
        }
    },
    data() {
        return {
            paused: null,
            closeMusic: null,
            playMusic: [],
            currentTime: 0,
            duration: 0,
            lyric:null,
            indexMusic: null,
            playlist: [],
        }
    },
    mounted() {
        let audio = this.$refs.audio ;
        audio.addEventListener("pause", ()=>{
            // console.log("");
            this.paused = true;
        });
        audio.addEventListener("play", ()=>{
            // console.log("");
            this.paused = false;
        });
        audio.addEventListener("durationchange", ()=>{
            // console.log("",audio.duration);
            this.duration = audio.duration
        });
        audio.addEventListener("timeupdate", ()=>{
            // console.log("",audio.currentTime);
            this.currentTime = audio.currentTime
            this.drawCircle(this.currentTime,this.duration);
        });
        audio.addEventListener("ended", ()=>{
            // console.log("",audio.ended);
            this.playNextRight()
        });
        
        if(this.currentMusic){
            this.playMusic = this.currentMusic.item
            this.indexMusic = this.currentMusic.index
            this.playlist = this.currentMusic.newSongs
        }

        this.axios.get(`/lyric?id=${this.playMusic.id}`).then((respaonse)=>{
            var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
            var arr = respaonse.data.lrc.lyric
                .split("\n")
                .filter((e) => e)
                .map((str) => {
                    var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
                    var timeArr = time.split(":");
                    time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
                    var text = str.replace(patt, "");
                    return { time, text };
                });
            this.lyric = arr
        });

    },
    methods: {
        drawCircle:function(n,total){
            let canvas = this.$refs.circle;
            let ctx = canvas.getContext("2d");

            ctx.clearRect(0,0,50,50);
            ctx.beginPath();
            ctx.lineWidth = 1;
            ctx.strokeStyle = "rgba(0,0,0,0.15)";
            ctx.arc(25,25,21,0,Math.PI * 2,false);
            ctx.stroke();
            ctx.closePath();

            ctx.beginPath();
            ctx.lineWidth = 2;
            ctx.strokeStyle = "rgba(0,0,0,0.85)";
            ctx.arc(25,25,21,Math.PI * -0.5,Math.PI * ((n/total)*2-0.5),false);
            ctx.stroke();
            ctx.closePath();
        },
        ClickPaused:function(){
            this.paused = !this.paused
            let audio = this.$refs.audio;
            if(this.paused){
               audio.pause();
            }else {
                audio.play();
            }
            this.$emit('click:paused',this.paused)
        },
        clickMusicPlayer:function(){
            this.closeMusic = true
            if(this.closeMusic){
                document.body.style.overflow = "hidden"
            }
        },
        closeMusicJudge:function(b){
            this.closeMusic = b
             if(!this.closeMusic){
                document.body.style.overflow = ""
            }
        },
        ClickSuspend:function(n){
            this.paused=n;
            let audio = this.$refs.audio;
            if(this.paused){
               audio.pause();
            }else {
                audio.play();
            }
        },
        // 
        calculateNextRight() {
            var nextIndex;
            if( this.indexMusic<this.playlist.length-1 ) {
                nextIndex = this.indexMusic +1 ;
            }else {
                nextIndex = 0 ;
            }
            return nextIndex
        },
        playNextRight() {
            var index = this.calculateNextRight();
            this.playMusic = this.playlist[index],
            this.indexMusic = index
            this.$emit("update:music", {
                index:this.indexMusic,
                item:this.playMusic,
                newSongs:this.playlist
            })
        },
        ClickRight() {
            this.playNextRight()
        },

        // 
        calculateNextLeft() {
            var nextIndex;
            if( this.indexMusic>0 ) {
                nextIndex = this.indexMusic -1 ;
            }else {
                nextIndex = this.playlist.length -1 ;
            }
            return nextIndex
        },
        playNextLeft() {
            var index = this.calculateNextLeft();
            this.playMusic = this.playlist[index],
            this.indexMusic = index
            this.$emit("update:music", {
                index:this.indexMusic,
                item:this.playMusic,
                newSongs:this.playlist
            })
        },
        ClickLeft() {
            this.playNextLeft()
        },
    },
    watch: {
        currentMusic: function(){
            this.playMusic = this.currentMusic.item
            this.indexMusic = this.currentMusic.index
            this.playlist = this.currentMusic.newSongs
        },
        paused: function(n){
            this.$emit("judge-paused",n);
        },
        playMusic(n) {
            this.axios.get(`/lyric?id=${n.id}`).then((respaonse)=>{
            var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
            var arr = respaonse.data.lrc.lyric
                .split("\n")
                .filter((e) => e)
                .map((str) => {
                    var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
                    var timeArr = time.split(":");
                    time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
                    var text = str.replace(patt, "");
                    return { time, text };
                });
            this.lyric = arr
            });
        },
    }
}
</script>

<style scoped lang="less">
    #MusicPlayer {
        audio {
            width: 100%;
            height: 30px;
            position: fixed;
            z-index: 999;
            bottom: 100px;
            left: 0px;
        }
        .MusicPlayer {
            width: 100%;
            padding: 10px 0;
            position: fixed;
            left: 0px;
            bottom: 0px;
            z-index: 999;
            background-color: #fcfffd;
            box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.45);
            display: flex;
            align-items: center;
            .player {
                flex: 1;
                padding: 0px 10px;
                display: flex;
                align-items: center;
                .img {
                    width: 50px;
                    left: 10px;
                    img {
                        width: 50px;
                        border-radius: 50%;
                        vertical-align: middle;
                        box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);
                    }
                }
                .text {
                    flex: 1;
                    padding: 0 10px;
                    h3 {
                        font-size: 18px;
                        color: #333333;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                    p {
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        .singer {
                            font-size: 13px;
                            color: #888888;
                        }
                    }
                }
                .suspend {
                    width: 50px;
                    height: 50px;
                    right: 10px;
                    position: relative;
                    .begin {
                        position: absolute;
                        left: 15px;
                        top: 13px;
                    }
                }
            }
        }
    }
    @keyframes rotate {

        0% {
            transform: rotateZ(0deg);
        }

        100% {
            transform: rotateZ(360deg);
        }

    }
    
</style>