<script>
export default {
    props:{
        paused:{
            type:Boolean
        },
        currentTime:{
            type:Number
        },
        duration:{
            type:Number
        },
        playMusic:{
            type:Object
        }
    },
    data: function(){
        return {
            max:100,
            value:0,
            judge:false
        }
    },
    methods: {
        MusicSuspend:function(){
            this.$emit('music-suspend',!this.paused);
        },
        getValue:function(e){
            this.$emit('music-currentTime',e.target.value);
        },
        MusicLeft:function(){
            this.$emit('music:left',this.playMusic)
        },
        MusicRight:function(){
            this.$emit('music:right',this.playMusic)
        }
    },
}
</script>

