<template>
    <li @click="clickMusic(item.first)" >
        <span>
            {{item.first}}
        </span>
    </li>
</template>

<script>
export default {
    props: {
        item:{
            type:Object
        }
    },
    methods: {
        clickMusic:function(n){
            // console.log(111);
            this.$emit('hot-searchClick',n)
        }
    },
}
</script>

<style scoped lang="less">
    li {
        line-height: 32px;
        display: inline-block;
        border: 1px solid #e7e8eb;
        border-radius: 16px;
        padding: 0 14px;
        font-size: 14px;
        margin-right: 8px;
        margin-bottom: 8px;
        color: #333333;
    }
</style>