<template>
    <li v-if="condition" >
        <span class="formerly"></span>
        <p>
            {{item}}
        </p>
        <span
            class="remove"
            @click="Delete"
        >
            <!-- <i></i> -->
        </span>
    </li>
</template>

<script>
export default {
    props: ["item"],
    data: function(){
        return {
            condition:true
        }
    },
    methods: {
        Delete:function(){
            this.condition = false;
            // console.log(this.item);
            this.$emit('delete-array-music',this.item)
        }
    },
}
</script>

<style scoped lang="less">
    li {
        width: 100%;
        line-height: 45px;
        display: flex;
        align-items:center;
        .formerly {
            display: inline-block;
            width: 15px;
            height: 15px;
            background: url('data:image/svg+xml;base64,') no-repeat ;
            margin: 0 10px;
            vertical-align: middle;
            margin-top: 6px;
        }
        p {
            width: 100%;
            font-size: 14px;
        }
        .remove {
            i {
                display: inline-block;
                width: 12px;
                height: 12px;
                background: url('data:image/svg+xml;base64,') no-repeat;
                padding: 10px 0;
            }
            width: 32px;
            height: 32px;
        }
    }
</style>