<template>
<div id="PlayPageConterText" @click="$emit('text:condition',true)" >
    
    <!-- () -->

    <div
        class="PlayPageConterText"
        :style="{marginTop:distance+'px'}"
    >
        <div
            v-if="lyric"
        >
            <p
                v-for="(item, index) in lyric"
                :key="index"
                :style="{
                    color:index===MusicIndex?'#fa163f':'',
                    fontSize:index===MusicIndex?'20px':'14px',
                    padding: index===MusicIndex?'22px':'12px',
                }"
            >
                <span>{{item.text===""?"~":item.text}}</span>
            </p>
        </div>
        <div
            v-else
        >
            <p></p>
        </div>
    </div>

</div>
</template>

<script>
export default {
    data() {
        return {
            distance:200,
            MusicIndex:0,
        }
    },
    props:{
        lyric:{
            type:Array
        },
        currentTime:{
            type:Number
        }
    },
    watch: {
        currentTime(n) {
            var i = this.lyric.findIndex((element)=>{
                return element.time >= n
            })
            this.MusicIndex = i

            if(i===-1){
                this.distance = -86 * this.lyric.length-1 + 200
            }else {
                this.distance = -86 * (i-1) + 200
            }

        }
    }
}
</script>

<style scoped lang="less">
    #PlayPageConterText {
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        .PlayPageConterText {
            height: 100%;
            margin-top: 300px;
            p {
                text-align: center;
                font-size: 14px;
                color: rgba(255, 255, 255, 0.8);
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 12px;
            }
        }
    }
</style>