<template>
  <div class="Recommed">

    <!--  -->

    <div class="min">
      
      <!--  -->
      <RecommendMusic
        :musicResultData="musicResultData"
        @judge="$emit('judge',$event)"
      ></RecommendMusic>

      <!--  -->
      <TheLatestMusic
        :newSongs="newSongs"
        :currentMusic="$attrs.currentMusic"
        :Paused="$attrs.Paused"
        @update-music="$emit('update-music',$event)"
      ></TheLatestMusic>

    </div>

    <!--  -->
    <MusicLogo></MusicLogo>

  </div>
</template>

<script>
import RecommendMusic from '@/components/RecommendMusic.vue';
import TheLatestMusic from '@/components/TheLatestMusic.vue';
import MusicLogo from '@/components/MusicLogo.vue';

export default {
  components: {
    RecommendMusic,
    TheLatestMusic,
    MusicLogo,
  },
  data: function(){
    return {
      recommends: [],
      newSongs: [],
    }
  },
  computed: {
    musicResultData:function(){
      return this.recommends.slice(0,6);
    },
  },
  created() {
    this.$emit('music:condition',true)
    this.axios.get("/personalized").then((respaonse)=>{
      this.recommends = respaonse.data.result;
    });
    this.$emit('music:condition',true)
    this.axios.get("/personalized/newsong").then((respaonse)=>{
      let result = respaonse.data.result
      for(let i=0;i<result.length;i++){
        let obj = {
          id:result[i].id,
          name:result[i].name,
          picUrl:result[i].picUrl,
          artists:result[i].song.artists
        };
        this.newSongs.push(obj);
        this.$emit('music:condition',false)
      }
    });
  },
}
</script>

<style scoped lang="less">
  .Recommed {
    padding-top: 42px;
    background-color: #fcfcfd;
    .min {
      min-height: 800px;
    }
  }
</style>
