<template>
    <!--  -->
    <li @click="navigateToPlayList(item.id)" >
        <div class="top_img">
            <img :src="item.picUrl" alt="">
            <span>{{ item.playCount | formatPlayCount }}</span>
        </div>
        <p>{{item.name}}</p>
    </li>
</template>

<script>
export default {
    props: {
        item: {
            type: Object,
        }
    },
    data: function(){
        return {
            Judge: false
        }
    },
    filters: {
        formatPlayCount: function (value) {
            return (value / 10000).toFixed(1) + "";
        },
    },
    methods: {
        navigateToPlayList(id) {
            // console.log(id);
            this.$emit('judge',{
                judge: this.Judge,
            })
            this.$router.push({
                path:"/recommendplaylist",
                query: {
                    id
                },
            })
        }
    },
}
</script>

<style scoped lang="less">
    li {
        flex:1 1 30%;
        &:nth-of-type(3n+2){
            margin-left: 3px;
            margin-right: 3px;
        }
        .top_img {
            position: relative;
            img {
                flex:1 1 30%;
                vertical-align: bottom;
            }
            span {
                position: absolute;
                right: 2px;
                top: 2px;
                font-size: 12px;
                color: #ffffff;
                text-shadow: 1px 0 0 rgba(0,0,0,0.15);
                background: url('data:image/svg+xml;base64,') no-repeat left center ;
                background-size: 13px;
                padding-left: 15px;
            }
        }
        p {
            padding: 6px 2px 0 6px ;
            font-size: 13px;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
            text-overflow: ellipsis;
            margin-bottom: 16px;
        }
    }
</style>