import Vue from 'vue'
import VueRouter from 'vue-router'
import Recommed from '../views/Recommed.vue'
import Search from '../views/Search.vue'
import RecommendPlaylist from '../views/RecommendPlaylist.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Recommed',
    component: Recommed
  },
  {
    path: '/hotsong',
    name: 'HotSong',
    // route level code-splitting
    // this generates a separate chunk (HotSong.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "HotSong" */ '../views/HotSong.vue')
  },
  {
    path: '/search',
    name: 'Search',
    component: Search
  },
  {
    path: '/recommendplaylist',
    name: 'RecommendPlaylist',
    component: RecommendPlaylist
  },
]

const router = new VueRouter({
  routes
})

export default router
