<template>
  <div class="MusicSearchBox">

      <!--  -->

      <div class="imput">
        <span></span>
        <input
            type="text"
            placeholder=""
            v-model.trim="search"
            @keyup.enter="SearchContent"
        >
        <i @click="eliminate" v-if="condition" >
            <svg t="1608368593747" class="icon" viewBox="0 0 1114 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2987" width="20" height="20"><path d="M715.861 393.444c-13.34-13.34-34.958-13.34-48.298 0l-84.48 84.48-84.48-84.48c-13.34-13.34-34.93-13.34-48.27 0-13.341 13.34-13.341 34.93 0 48.27l84.48 84.48-84.48 84.48c-13.341 13.34-13.341 34.958 0 48.27 13.34 13.34 34.93 13.34 48.27 0l84.48-84.48 84.48 84.48c13.34 13.34 34.958 13.34 48.298 0.028 13.312-13.34 13.312-34.958 0-48.298l-84.48-84.48 84.48-84.48c13.312-13.34 13.312-34.93 0-48.27zM583.083 99.527c-235.634 0-426.667 191.033-426.667 426.667S347.449 952.86 583.083 952.86c235.662 0 426.666-191.032 426.666-426.666S818.716 99.527 583.083 99.527z m0 785.067c-197.917 0-358.4-160.455-358.4-358.4 0-197.917 160.483-358.4 358.4-358.4 197.945 0 358.4 160.483 358.4 358.4 0 197.945-160.455 358.4-358.4 358.4z m0 0" p-id="2988" fill="#bcbdbd"></path></svg>
        </i>
      </div>
  </div>
</template>

<script>
export default {
    props:{
        hotSearchClick:{
            type:String
        }
    },
    data: function (){
        return {
            search:"",
            condition:false
        }
    },
    methods: {
        SearchContent:function(){
            if(this.search==""){
                return ;
            }
            this.condition=true
            this.$emit("search-content",this.search);
        },
        eliminate:function(){
            this.search=""
            this.condition=false
            this.$emit("search-condition",this.condition);
        }
    },
    watch:{
        hotSearchClick:function(){
            this.search = this.hotSearchClick
            this.$emit("search-content",this.search);
            this.condition=true
        },
    }
}
</script>

<style scoped lang="less">
    .MusicSearchBox {
        border-bottom: 1px solid #e6e7e8;
        .imput {
        padding: 15px 10px;
        position: relative;
        span {
            position: absolute;
            left: 20px;
            top: 23px;
            background: url('data:image/svg+xml;base64,');
            width: 13px;
            height: 13px;
        }
        input {
            width: 100%;
            height: 30px;
            outline: none;
            border: none;
            padding: 0 30px;
            border-radius: 30px;
            background-color: #ebecec;
        }
        i {
            position: absolute;
            right: 20px;
            top: 20px;
        }
        }
    }
</style>