<template>

    <!--  -->

    <div id="TheLatestMusic">
        <Headline></Headline>

        <ul>
            <PlayListBottom
                v-for="(item, index) in newSongs"
                :key="index"
                :item="item"
                :index="index"
                :Paused="$attrs.Paused"
                :currentMusic="$attrs.currentMusic"
                @update-music="music"
            ></PlayListBottom>
        </ul>
    </div>
</template>

<script>
import Headline from '@/components/Headline.vue';
import PlayListBottom from '@/components/PlayListBottom.vue';

export default {
    props: {
        newSongs: {
            type: Array
        },
    },
    data: function(){
        return {
        }
    },
    components: {
        Headline,
        PlayListBottom,
    },
    methods: {
        music(n) {
            this.$emit('update-music',{
                item:n.item,
                index:n.index,
                newSongs:this.newSongs,
            })
        }
    },
}
</script>

<style scoped lang="less">
    #TheLatestMusic {
        background-color: #fcfcfd;
    }
</style>