<template>
  <div class="Search">
    
    <!--  -->

    <MusicSearchBox
      @search-content="content"
      @search-condition="condition=$event"
      :hotSearchClick="hotSearchClick"
    ></MusicSearchBox>

    
    <SearchMusicContent
      v-if="condition"
      :currentMusic="$attrs.currentMusic"
      :MusicSongsResult="MusicSongsResult"
      :Paused="$attrs.Paused"
      @update-music="music"
    ></SearchMusicContent>
    <MusicSearchBottom
      v-else
      :MusicHotsResult="MusicHotsResult"
      :value="value"
      @hot-searchClick="hotSearchClick = $event"
      @delete-array-music="deleteArrayMusic=$event"
    >
    </MusicSearchBottom>

  </div>
</template>

<script>
import MusicSearchBox from '@/components/MusicSearchBox.vue';
import MusicSearchBottom from '@/components/MusicSearchBottom.vue';
import SearchMusicContent from '@/components/SearchMusicContent.vue';

export default {
    components: {
      MusicSearchBox,
      MusicSearchBottom,
      SearchMusicContent,
    },
    data: function() {
      return {
        condition: false,
        hotSearchClick: "",
        deleteArrayMusic: "",
        value: [],
        MusicHotsResult: [],
        MusicSongsResult: [],
      }
    },
    created() {
      this.$emit('music:condition',false)
      this.axios.get("/search/hot").then((respaonse)=>{
        this.MusicHotsResult = respaonse.data.result.hots
      });
    },
    methods: {
      content:function(search){
        this.$emit('music:condition',true)
        if(search){
          this.condition=!this.condition
          this.MusicSongsResult = []
        }
        if(this.value.indexOf(search)===-1){
          this.value.push(search);
        }
        this.axios.get(`/search?keywords=${search}`).then((respaonse)=>{
          var result = respaonse.data.result.songs
          // console.log(result);
          for(let i=0;i<result.length;i++){
            this.axios.get("/song/detail?ids="+result[i].id).then((respaonse)=>{
              var songs = respaonse.data.songs[0]
              var obj = {
                id:songs.id,
                name:songs.name,
                picUrl:songs.al.picUrl,
                artists:songs.ar
              };
              this.MusicSongsResult.push(obj);
              this.$emit('music:condition',false)
            })
          }
        });
      },
      music: function(n) {
          this.$emit('update-music',{
              item:n.item,
              index:n.index,
              newSongs:this.MusicSongsResult,
          })
      },
    },
    watch:{
      deleteArrayMusic:function(n){
        this.value.splice( this.value.indexOf(n) , 1 )
      },
    }
}
</script>

<style scoped lang="less">
  .Search {
    padding-top: 42px;
    background-color: #fcfcfd;
  }
</style>