<template>
    <div id="RecommendPlaylist" >
        <div class="backtrack" >
            <svg @click="MusicReturn" t="1608293446727" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3001" width="20" height="20"><path d="M766.862 1021.724c-10.24 0-20.594-3.64-28.444-10.922L228.693 538.396c-15.701-14.563-15.701-38.229 0-52.792L738.418 13.198c15.701-14.563 41.187-14.563 56.889 0 15.701 14.564 15.701 38.23 0 52.793L314.027 512l481.28 446.009c15.701 14.563 15.701 38.23 0 52.793-7.851 7.282-18.205 10.922-28.445 10.922z" p-id="3002" fill="#ffffff"></path></svg>
        </div>
        
        <MusicHitList
            :currentMusic="$attrs.currentMusic"
            :DataContent="DataContent"
            :item="item"
            :Paused="$attrs.Paused"
            @update-music="music"
        ></MusicHitList>

    </div>
</template>

<script>
import MusicHitList from '@/components/MusicHitList.vue'

export default {
    components:{
        MusicHitList,
    },
    data() {
        return {
            DataContent: [],
            item: null,
            id: null,
        }
    },
    methods: {
        MusicReturn() {
            this.$emit('music-return',true)
            this.$router.go(-1)
        },
        music(n) {
            this.$emit('update-music',{
                item:n.item,
                index:n.index,
                newSongs:this.DataContent,
            })
        }
    },
    activated() {
        this.$emit('music-return',false)
        this.$emit('music:condition',true)
        var id = this.$router.history.current.query.id
        this.axios.get("/playlist/detail?id="+id).then((respaonse)=>{
            let trackIds = respaonse.data.playlist.trackIds
            this.item = respaonse.data.playlist
            var arr = []
            for(let i=0; i<trackIds.length; i++){
                arr.push(trackIds[i].id)
            }
            this.axios.get(`/song/detail?ids=${arr.join(",")}`).then((respaonse)=>{
                var result = respaonse.data.songs
                this.DataContent = []
                for(let i=0;i<result.length;i++){
                    var obj = {
                        id:result[i].id,
                        name:result[i].name,
                        picUrl:result[i].al.picUrl,
                        artists:result[i].ar
                    };
                    this.DataContent.push(obj)
                    this.$emit('music:condition',false)
                }
            });
        });
    },
}
</script>

<style scoped lang="less">
    #RecommendPlaylist {
        width: 100%;
        .backtrack {
            width: 100%;
            background-color: #1b262c;
            padding-left: 10px;
            .icon {
                margin: 6px 0;
            }
        }
    }
</style>