<template>
    <div id="PlayPage">

        <!--  -->

        <div class="PlayPage" :style="'background-image: url('+playMusic.picUrl+')'" ></div>

        <PlayPageTop
            @close-music='$emit("close-music",$event)'
        >
            {{playMusic.name}}
        </PlayPageTop>

        <PlayPageConter
            :lyric="$attrs.lyric"
            :currentTime="$attrs.currentTime"
            :playMusic="playMusic"
            :paused="$attrs.paused"
        ></PlayPageConter>

        <PlayPageBottom
            :paused="$attrs.paused"
            :currentTime="$attrs.currentTime"
            :duration="$attrs.duration"
            :playMusic="playMusic"
            @music:left="$emit('music:left',$event)"
            @music:right="$emit('music:right',$event)"
            @music-currentTime="$emit('music-currentTime',$event)"
            @music-suspend="$emit('music-suspend',$event)"
        ></PlayPageBottom>
    </div>
</template>

<script>
import PlayPageTop from '@/components/PlayPageTop.vue'
import PlayPageConter from '@/components/PlayPageConter.vue'
import PlayPageBottom from '@/components/PlayPageBottom.vue'

export default {
    components: {
        PlayPageTop,
        PlayPageConter,
        PlayPageBottom
    },
    props:{
        playMusic:{
            type:Object
        }
    },
}
</script>

<style scoped lang="less">
    #PlayPage {
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0px;
        top: 0px;
        z-index: 1000;
        background-color: #f1fcfc;
        .PlayPage {
            width: 100%;
            height: 100%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            filter: blur(30px) brightness(0.5);
            position: absolute;
            left: 0px;
            top: 0px;
            z-index: -1;
        }
    }
</style>